.hidden {
    visibility: hidden; 
    opacity: 0;
    /* Need to fix src/components/Header/Marquee/index.js for this to work... otherwise flashes
    when navigating from home to another page */
    /* transition: visibility 0s .4s, opacity .4s ease-out; */
}

.visible {
    visibility: visible;
    opacity: 1;
    transition: opacity .4s ease-out;
}

/* scrolled */

.sticker-marquee-scrolled svg {
    width: 140px;
}

.menu-scrolled {
    margin-left: 52px;
    margin-top: 6px;
}

.menu-scrolled div {
    display: flex;
    margin-right: 25px;
}

.menu-scrolled a {
    font-size: 1.5rem;
}

/* unscrolled */

.header {  
    padding: 20px 0 0 0;
    margin-top: -30px;
    /* margin-top: 35px; */
    width: 100%;
    height: 100px;
    background: linear-gradient(0deg, rgba(255, 255, 255, 0), rgba(255, 255, 255, .9), rgba(255, 255, 255, 1));
    display: flex;
    position: fixed;
    z-index: 1;
}
  
.sticker-marquee {
    display: flex;
    flex-direction: column;
    position: fixed;
}

.sticker-marquee svg {
    padding-bottom: 20px;
    width: 295px;
}

.menu {
    margin-top: -13px;
    font-size: 3rem;
    position: absolute;
    margin-left: 344px;
}